.main-nav__toggle {
  width: 22px;
  height: 22px;
  position: absolute;
  z-index: 3;
  display: block;
  border: none;
  cursor: pointer;
  background-color: #ffffff;
  top: 17px;
  right: 22px;
}
@media (min-width: 768px) {
